.crew__text-bio {
    font-family: 'Barlow', Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #D0D6F9;
    max-width: 444px;
    margin: 25px 0 0;
}

@media screen and (max-width: 1279px) {
    .crew__text-bio {
        font-size: 16px;
        line-height: 28px;
        margin: 0 auto;
        max-width: 458px;
        text-align: center;
    }
}

@media screen and (max-width: 767px) {
    .crew__text-bio {
        font-size: 15px;
        line-height: 25px;
        max-width: 327px;
        padding: 0 0 50px;
    }
}
