.technology__block {
    display: flex;
    margin: 137px 0 0 0;
}


@media screen and (max-width: 1279px) {
    .technology__block {
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 auto;
    }
}

