.destination__list {
    display: flex;
    list-style: none;
    margin: 160px 0 0 0;
    padding: 0;
}


@media screen and (max-width: 1279px) {
    .destination__list {
        margin: 0 0 0 30px;
        padding: 0;
        align-items: flex-start;
        justify-content: center;
    }
}
